import { Component, HostListener } from '@angular/core';

interface Food {
  value: string;
  viewValue: string;
}
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})




export class AppComponent {

  getmoney= 15000
  loanamount=100000;
  loanlimit:any



  foods: Food[] = [
    {value: 'steak-0', viewValue: '3 Months'},
    {value: 'pizza-1', viewValue: '6 Months'},
    {value: 'tacos-2', viewValue: '12 Months'}
  ];
  currentScrollPos:any
  prevScrollpos:any
  temp:any;


  Income: number = 0;
  updateSetting(event) {
    this.Income = event.value;

  }

  Expense: number = 0;
  updateSetting1(event) {
    this.Expense = event.value;
    this.getloan()

  }
  getloan(){
 
    this.loanlimit = this.Income-this.Expense
    console.log(this.loanlimit)
    if(this.loanlimit>=15000){
      var element = <HTMLInputElement> document.getElementById("get_money");

      element.disabled = false;
    }
    else{
      var element = <HTMLInputElement> document.getElementById("get_money");

      element.disabled = true;

    }
  
  }
 

  




  @HostListener('window:scroll', ['$event']) getScrollHeight() {
  this.prevScrollpos = window.pageYOffset;

  this.currentScrollPos = window.pageYOffset;
  
  // console.log(this.temp> this.currentScrollPos)


  if (this.temp > this.currentScrollPos && this.temp<5) {
          document.getElementById("header").style.top = "0";
          document.getElementById("header").style.backgroundColor= "transparent";
          document.getElementById("header").style.boxShadow="none";
          // document.getElementById("pro").style.padding = "3%"
          // document.getElementById("sol").style.padding = "3%"
          // document.getElementById("pri").style.padding = "3%"
          // document.getElementById("lo").style.padding = "3%"
          // document.getElementById("gs").style.padding = "3%"
          // document.getElementById("im").style.padding = "3%"


      
        } else {
          document.getElementById("header").style.top = "0";
          document.getElementById("header").style.backgroundColor= "#fff";
          document.getElementById("header").style.boxShadow=" 0px 2px 10px -1px #beb7b749";

          // document.getElementById("pro").style.padding = "0"
          // document.getElementById("sol").style.padding = "0"
          // document.getElementById("pri").style.padding = "0"
          // document.getElementById("lo").style.padding = "0"
          // document.getElementById("gs").style.padding = "0"

        }
        this.temp = this.currentScrollPos;
        this.prevScrollpos = this.currentScrollPos;
        // console.log("3",this.prevScrollpos)
      }

  





}
